import React from 'react';



import {
  BrowserRouter as Router,
  Route,
  Link
} from 'react-router-dom';

//components
import Header from './component/headerComponent/header';
import Footer from './component/headerComponent/footerComponent/footer';
import HomePage from './component/headerComponent/pages/homePage';
import MyPlayedGames from './component/headerComponent/pages/MyPlayedGames';
import Login from './component/headerComponent/pages/Login';
import UserStore from './stores/UserStore';
import LoginForm from './LoginForm';

import SubmitButton from './SubmitButton';

import './Assets/scss/default.scss';

//observer
import {observer} from 'mobx-react';



class App extends React.Component {

  async componentDidMount(){
    try{
        let res = await fetch ('/isLoggedIn',{
          method: 'post',
          headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
          }
        });

        let result = await res.json();

        if (result && result.success){
          UserStore.loading = false;
          UserStore.isLoggedIn = true;
          UserStore.username = result.username;
        }
        
        else {
          UserStore.loading = false;
          UserStore.isLoggedIn = false;

        }
    }
    catch(e){

      UserStore.loading = false;
      UserStore.isLoggedIn = false;
    }
  }

  async doLogout(){
    try{
        let res = await fetch ('/logout',{
          method: 'post',
          headers: {
            'Accept': 'application/json',
            'Content-type': 'application/json'
          }
        });

        let result = await res.json();

        if (result && result.success){
         
          UserStore.isLoggedIn = false;
          UserStore.username = '';
          
        }
        

    }
    catch(e){

    console.log(e)
    }
  }
  render(){

    if (UserStore.loading){
      return (
        <div className = "app">
          <div className = "container">
            loading, please wait...
          </div>
        </div>
      );
    }

    else {
    if (UserStore.isLoggedIn){
      return (
        <div className = "app">
          <div className = "container">
            Welcome {UserStore.username}

            <SubmitButton
            text = {'log out'}
            disable = {false}
            onClick = { () => this.doLogout() }
            />
          </div>
        </div>
      );
    }
  }
  return (


    <Router>
    <div className="App">
       
       <Header/>
        <LoginForm/>

       <Route exact path='/' component={HomePage} />
       <Route exact path='/MyPlayedGames' component={MyPlayedGames} />
       <Route exact path = '/Login' component={Login}/>

       <Footer/>
    </div>
    </Router>
  );
}
}

export default observer(App);
